// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import BootstrapVue from 'bootstrap-vue'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import {store} from './store/store'
import VueHighcharts from 'vue-highcharts'
import moment from 'moment'
import VueMomentJS from 'vue-momentjs'
import Highcharts from 'highcharts'
import Toasted from 'vue-toasted'
import Icon from 'vue-awesome/components/Icon'
import pouchdb from 'pouchdb'
import VueWorker from 'vue-worker'
import SecureLS from 'secure-ls'
import publicIp from 'public-ip'
// load these modules as your need
import loadStock from 'highcharts/modules/stock'
import loadMap from 'highcharts/modules/map'
import loadDrilldown from 'highcharts/modules/drilldown'
// some charts like solid gauge require `highcharts-more.js`, you can find it in official demo.
import loadHighchartsMore from 'highcharts/highcharts-more'
import loadSolidGauge from 'highcharts/modules/solid-gauge'
import ip from 'ip'
import StarRating from 'vue-star-rating'

loadStock(Highcharts)
loadMap(Highcharts)
loadDrilldown(Highcharts)
loadHighchartsMore(Highcharts)
loadSolidGauge(Highcharts)
Vue.use(VueMomentJS, moment)
Vue.use(BootstrapVue)
Vue.use(VueHighcharts, {Highcharts})
Vue.use(Toasted,{iconPack : 'fontawesome'})
Vue.use(VueWorker)

pouchdb.plugin(require('pouchdb-erase'))
let db = new pouchdb('db')
let hdb = new pouchdb('history')
var ls = new SecureLS({encodingType: 'aes'})

Object.defineProperty(Vue.prototype, '$db', { value: db })
Object.defineProperty(Vue.prototype, '$hdb', { value: hdb })
Object.defineProperty(Vue.prototype, '$baseIp', { value: baseIp })
Object.defineProperty(Vue.prototype, '$ls', { value: ls })
Vue.component('star-rating', StarRating)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  store,
  el: '#app',
  router,
  components: { App },
  template: '<App/>',
  beforeDestroy(){
    console.log("destroyed")
    new pouchdb('db').destroy().then(function () {
    }).catch(function (err) {
    })
  }
})

