<template>
  <div class="Job Detail">
    <b-container fluid>
      <h3><u>JOB DETAIL</u></h3>
      <b-row>
        <b-col align-self="center" cols="4">
           <b-carousel id="carousel1"
                controls
                indicators
                background="#ababab"
                :interval="3000"
                img-width="400"
                img-height="400"
                v-model="slide"
                @sliding-start="onSlideStart"
                @sliding-end="onSlideEnd">

      <b-carousel-slide :img-src=jobPic1 ></b-carousel-slide>
      <b-carousel-slide :img-src=jobPic2></b-carousel-slide>
      </b-carousel>
      </b-col>
      <b-col>
         <b-row>
          <b-col class="hlist">JOB ID: </b-col>
          <b-col>{{jobId}}</b-col>
        </b-row>
        <b-row>
          <b-col class="hlist">JOB TYPE:</b-col>
          <b-col>{{jobType}}</b-col>
        </b-row>
        <b-row>
          <b-col class="hlist">MATERIAL:</b-col>
          <b-col>{{material}}</b-col>
        </b-row>
        <b-row>
          <b-col class="hlist">PROVIDED</b-col>
          <b-col>{{provided}}</b-col>
        </b-row>
        <b-row>
          <b-col class="hlist">QUANTITY</b-col>
          <b-col>{{quantity}}</b-col>
        </b-row>
        <b-row>
          <b-col class="hlist">TURN AROUND TIME</b-col>
          <b-col>{{tat}} Days</b-col>
        </b-row>
        <b-row>
          <b-col class="hlist">SCALE</b-col>
          <b-col>{{scale}}</b-col>
        </b-row>
        <b-row>
          <b-col class="hlist">HEIGHT</b-col>
          <b-col>{{height}}</b-col>
        </b-row>
        <b-row>
          <b-col class="hlist">WIDTH</b-col>
          <b-col>{{width}}</b-col>
        </b-row>
        <b-row>
          <b-col class="hlist">LENGTH</b-col>
          <b-col>{{length}}</b-col>
        </b-row>
        <b-row>
          <b-col class="hlist">THICKNESS</b-col>
          <b-col>{{thick}}</b-col>
        </b-row>
        <b-row>
          <b-col class="hlist">DESCRIPTION</b-col>
          <b-col>
            <b-card class="text-center">
              {{description}}
            </b-card>
          </b-col>
        </b-row>
        <b-row>
          <b-col class="hlist">TIME</b-col>
          <b-col>{{createdAt}}</b-col>
        </b-row>
        <b-row style="margin-top:15px" v-if="cat === 'provider'">
          <b-col>
            <b-button color="green" size="lg" :block=true  :href=dxfFile target="_blank">DXF FILE Download</b-button>
          </b-col>
        </b-row>
        <b-row style="margin-top:15px">
          <b-col v-if="bidView === true && cat === 'provider'">
            <b-btn :block=true size="lg" :variant="variant" v-b-modal.modal1>QUOTE</b-btn>
          </b-col>
          <b-col v-if="bidView === false &&  cat === 'provider'">
            <b-btn :block=true size="lg" :variant="variant" @click="yourQuote()">YOUR QUOTE</b-btn>
          </b-col>
        </b-row>
      </b-col>
    </b-row>
    <hr>
    <b-row v-if="cat === 'seeker'">
      <b-col>
        <b-table v-if="items.length > 0" striped hover :fields="fields" :items="items" :current-page="currentPage" :per-page="perPage" >
          <template slot="detailBid" slot-scope="cell">
          <!-- We use click.stop here to prevent a 'row-clicked' event from also happening -->
          <b-button size="sm" variant="info" @click.stop="bidDetails(cell.item,cell.index,$event.target)">Detail</b-button>
          </template>
        </b-table>
        <b-pagination align="center" v-if="items && items.length > 0" :total-rows="items.length" :per-page="perPage" v-model="currentPage" />
      </b-col>
    </b-row>
    <!--<b-row v-else>
      <b-col cols="4">
        <h4><u>Your Bid</u></h4>
        <div>
        <b-row>
          <b-col>UNIT PRICE</b-col>
          <b-col>{{unitPrice}}</b-col>
        </b-row>
        <b-row>
          <b-col>TOTAL PRICE</b-col>
          <b-col>{{unitPrice}}</b-col>
        </b-row>
        <b-row>
          <b-col>DESCRIPTION</b-col>
          <b-col>{{pd}}</b-col>
        </b-row>
        </div>
      </b-col cols="2">
      <b-col>

      </b-col>
    </b-row> -->
       <!--<b-row>
        <b-col cols="6">
          <b-table stacked :items="items">
            <template slot="jobPic1" slot-scope="data">
                <b-img-lazy :src=data.item.jobPic1 fluid alt="Responsive image" />
            </template>
            <template slot="jobPic2" slot-scope="data">
                <b-img-lazy :src=data.item.jobPic2 fluid alt="Responsive image" />
            </template>
            <template slot="dxfFile" slot-scope="data">
                <b-button size="sm" class="mr-2" :href=data.item.dxfFile target="_blank">Download</b-button>
            </template>
          </b-table>
        </b-col>
    </b-row> */ -->
    </b-container>
    <b-modal id="modal1" title="Quote" hide-footer ref="myModalRef">
        <b-form @submit="quoteSubmit">
      <b-form-group id="unitPrice">
        <b-form-input type="number"
                      v-model="form.unitPrice"
                      required
                      placeholder="Enter Unit Price">
        </b-form-input>
      </b-form-group>
      <b-form-group>
        <b-form-input type="number"
                      v-model="form.tat"
                      required
                      placeholder="Please mention Turn Around Time in Days">
        </b-form-input>
      </b-form-group>
      <b-form-group>
        <b-form-textarea type="text"
                      v-model="form.description"
                      required
                      :rows="3"
                      :max-rows="6"
                      placeholder="FeedBack/Ask">
        </b-form-textarea>
      </b-form-group>
      <b-button type="submit" variant="primary"  >Submit</b-button>
    </b-form>
    </b-modal>
    <b-navbar fixed="bottom">
      <span >Copyrights @ Upyog 2018</span>
    </b-navbar>
  </div>
</template>

<script>
import {store} from '../store/store'
export default {
  name: 'JobDetail',
  data () {
    return {
      baseUrl: '',
      token : '',
      form :{
        unitPrice:'',
        description:'',
        tat : ''
      },
      bid: '',
      variant : 'success',
      bidView : false,
      unitPrice : '',
      totalPrice : '',
      pd : '',
      cat : '',
      msg: 'Welcome to Your Job Detail Vue.js App',
      slide: 0,
      sliding: null,
      id : '',
      jid : '',
      jobId: "d0fc5a10-30d0-11e8-bd93-69d14ca657ef",
      active: true,
      views: [],
      seekerId: "eb4796d0-30c8-11e8-ab56-4ff618d987d1",
      jobType: "laser_cutting",
      material: "SS",
      provided: true,
      quantity: 500,
      tat: 10,
      scale: "CM",
      height: 10.2,
      width: 9.2,
      length: 5,
      thick: 1,
      description: "Urgent",
      credit: 20,
      jobPic1: "http://res.cloudinary.com/upyog/image/upload/v1522051522/sample2.jpg",
      jobPic2: "http://res.cloudinary.com/upyog/image/upload/v1522051515/sample1.jpg",
      dxfFile: "http://res.cloudinary.com/upyog/raw/upload/v1522051698/mample.dxf",
      status: "open",
      createdAt: "2018-03-26T14:06:45+05:30",
      updatedAt: "2018-03-26T14:06:45+05:30",
      perPage :10,
      currentPage:1,
      fields :["unitPrice","totalPrice","description","status","createdAt","detailBid"],
      items :[],
      userId : '',
      self : this
      }
    },
  created(){
    this.baseUrl = this.$baseIp()
    this.cat = store.getters.getCat
    this.id = this.$route.params.jobId
    this.token = store.getters.getToken
    console.log(this.id)
    let self = this
    this.$db.get(this.id).then(function(a){
      self._id = a._id
      self.jobId = a.jobId
      self.active =  a.active || null
      self.views = a.views || null
      self.seekerId =  a.seekerId || null
      self.jobType = a.jobType || null
      self.material = a.material || null
      self.provided = a.provided || null
      self.quantity = a.quantity || null
      self.tat= a.tat || null
      self.scale = a.scale || null
      self.height= a.height || null
      self.width= a.width || null
      self.length = a.length || null
      self.thick =  a.thick || null
      self.description = a.description || null
      self.credit= a.credit || null
      self.jobPic1 = a.jobPic1 || null
      self.jobPic2 = a.jobPic2 || null
      self.dxfFile = a.dxfFile || null
      self.status = a.status || null
      self.createdAt = a.createdAt || null
      self.updatedAt = a.updatedAt || null
      self.userId = a.userId || null
      if(self.cat === 'seeker'){
        self.$http.get(self.baseUrl+`bid/v1/ops/job/${self.jobId}`,{headers:{'Content-Type':"application/json",authtoken:self.token}}).then(response => {
          if(response.body.data.length === 0){
            self.$toasted.show('No Bids For Job Yet',{ type : 'info', icon : {
              name : 'info'
            }}).goAway(5000)
          }else{
            console.log(response.body.data)
            self.items = response.body.data
          }
        }, response =>{
          console.log(response)
        })
      }else{
        self.$http.get(self.baseUrl+`bid/v1/ops/job/provider/${self.jobId}`,{headers:{'Content-Type':"application/json",authtoken:self.token}}).then(response => {
          console.log(response.body)
          if(response.body.data === null){
            self.bidView = true
            self.toastme("You have not quoted for this JOB bid yet",'info')
          }else{
            self.unitPrice = response.body.data.unitPrice
            self.totalPrice = response.body.data.TotalPrice
            self.pd = response.body.data.description
            self.bid = response.body.data.bidId
          }

        }, response =>{
          self.$toasted.show('Please Try Again Later',{ type : 'warn', icon : {
            name : 'cancel',
            after : true // this will append the icon to the end of content
          }}).goAway(5000)
        })
      }
    },self).catch((e)=>{console.log(e)})
  },
  methods: {
    onSlideStart (slide) {
      this.sliding = true
    },
    onSlideEnd (slide) {
      this.sliding = false
    },
    bidDetails(a,b,c){
      this.$router.push({name:'BidDetail',params:{bidId:a.bidId}})
    },
    toastme(msg,ic){
      this.$toasted.show(msg,{ type : 'info', icon : {
            name : ic
          }}).goAway(5000)
    },
    yourQuote(){
      this.$router.push({name:'BidDetail',params:{bidId:this.bid}})
    },
    quoteSubmit(evt){
      this.$refs.myModalRef.hide()
      evt.preventDefault();
      //console.log(this.form.description)
      let o = {
        unitPrice : this.form.unitPrice,
        description : this.form.description,
        tat : this.form.tat,
        jobId : this.jobId,
        seekerId : this.userId,
        quantity : this.quantity
      }
      console.log(this.token)
      this.$http.post(this.baseUrl+`bid/v1/ops`,{o},{headers:{'Content-Type':"application/json",authtoken:this.token}}).then(response => {
        if(response.status === 226){
          this.toastme('You have already quoted','info')
        }else{
          this.toastme('You have successfully quoted','info')
          this.$router.go(this.$router.currentRoute)
        }
      },response=>{
        this.toastme('Please try again later','info')
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.hlist {
  font-weight:bold;

}
</style>



