<template>
  <div class="NewJob">
    <b-container style="margin-top:15px">
      <b-row align-h="center">
        <b-col cols="5">
            <b-form>
              <h3>New Job</h3>
              <b-form-group>
                <b-form-select v-model="job.jobType" :options="options" class="">
                </b-form-select>
                <div>Selected: <strong>{{ job.jobType }}</strong></div>
              </b-form-group>
            </b-form>
        </b-col>
      </b-row>
      <hr>
      <b-row align-h="center" v-if="job.jobType === 'laser_cutting'">
        <b-col cols="4">
          <b-form-group>
              <b-form-select v-model="job.scale"  required :options="scaleOptions" class="" />
              <div>Selected: <strong>{{ job.scale }}</strong></div>
          </b-form-group>
          <b-form-group>
              <b-form-select v-model="job.material"  required :options="mOptions" class="" />
              <div>Selected: <strong>{{ job.material }}</strong></div>
          </b-form-group>
          <b-form-group>
                  <b-form-input id="exampleInput1"
                              type="email"
                              v-model="job.quantity"
                              required
                              placeholder="Enter Quantity">
                  </b-form-input>
                </b-form-group>
          <b-form-group>
                  <b-form-input id="exampleInput1"
                              type="number"
                              v-model="job.thick"
                              required
                              placeholder="Enter Thickness">
                  </b-form-input>
                </b-form-group>
                <b-form-group label-text-align="left"
                  description="*Number of days in which job has to be delivered">
                  <b-form-input id="exampleInput1"
                              type="email"
                              v-model="job.tat"
                              required
                              placeholder="Turn Around Time in days">
                  </b-form-input>
                </b-form-group>
        </b-col>
        <b-col cols="4">
          <b-form-group>
            <b-form-file accept="image/*" v-model="job.jobPic1" :state="Boolean(job.jobPic1)" placeholder="Choose Job Pic"></b-form-file>
            <div class="mt-3">Selected Job Pic: {{job.jobPic1 && job.jobPic1.name}}</div>
          </b-form-group>
          <b-form-group>
            <b-form-file accept="image/*" v-model="job.jobPic2" :state="Boolean(job.jobPic2)" placeholder="Choose Job Pic"></b-form-file>
            <div class="mt-3">Selected Job Pic: {{job.jobPic2 && job.jobPic2.name}}</div>
          </b-form-group>
          <b-form-group>
            <b-form-file v-model="job.dxfFile" :state="Boolean(job.dxfFile)" placeholder="Choose DXF FILE"></b-form-file>

          </b-form-group>
          <b-form-group>
            <b-form-textarea id="textarea1"
                  v-model="job.description"
                  placeholder="Enter Job Description"
                  :rows="3"
                  :max-rows="6">
            </b-form-textarea>
          </b-form-group>
        </b-col>
      </b-row>
      <b-row align-h="center" v-if="selected === 'bending'">
          <b-col></b-col>
      </b-row>
      <b-row align-h="center" v-if="selected === 'stamping'">
          <b-col></b-col>
      </b-row>
      <b-row align-h="center" v-if="selected === 'shearing'">
          <b-col></b-col>
      </b-row>
      <b-row align-h="center" v-if="selected === 'powder_coating'">
          <b-col></b-col>
      </b-row>
      <b-form @submit="onSubmit" >
        <b-button type="submit" :block=true variant="primary">Submit</b-button>
      </b-form>
    </b-container>
    <PulseLoader  class="loading" v-if="view === false"></PulseLoader>
    <b-navbar fixed="bottom">
      <span >Copyrights @ Upyog 2018</span>
    </b-navbar>
  </div>
</template>

<script>
import axios from 'axios'
import PulseLoader from 'vue-spinner/src/PulseLoader.vue'
import {store} from '../store/store'
export default {
  name: 'NewJob',
  data () {
    return {
      baseUrl:'',
      token :'',
      cat : '',
      view : true,
      msg: 'Welcome to Your New Job Vue.js App',
      form:{
      email:'',
      name:'',
      },
      jobId: "d0fc5a10-30d0-11e8-bd93-69d14ca657ef",
      active: true,
      views: [],
      seekerId: "eb4796d0-30c8-11e8-ab56-4ff618d987d1",
      job : {
        jobType: null,
        material: null,
        provided: true,
        quantity: null,
        tat: null,
        scale: null,
        height: null,
        width: null,
        length: null,
        thick: null,
        description: null,
        credit: 20,
        jobPic1: null,
        jobPic2: null,
        dxfFile: null,
      },
      selected : 'null',
       options: [
        { value: null, text: 'Please select job type' },
        { value: 'laser_cutting', text: 'Laser Cutting' },
        { value: 'bending', text: 'Bending' },
        { value: 'stamping', text: 'Stamping/Blanking' },
        { value: 'powder_coating', text: 'Powder Coating'},
        { value: 'shearing', text:"Shearing"}
      ],
      scaleSelected: null,
      scaleOptions: [
        { value: null, text: 'Please select Scale Type' },
        { value: 'cm', text: 'CM' },
        { value: 'mm', text: 'MM' },
        { value: 'inch', text: 'INCH' }
      ],
      mSelected: null,
      mOptions: [
        { value: null, text: 'Please select Material Type' },
        { value: 'ss', text: 'Stainless Steel' },
        { value: 'ms', text: 'Mild Steel' },
        { value: 'al', text: 'Aluminium' }
      ]
    }
  },
  created(){
    this.baseUrl = this.$baseIp()
    this.token = store.getters.getToken
    this.cat = store.getters.getCat
    if(this.cat === 'provider'){
      this.toastme('You dont have permission to POST A JOB !','info')
      this.$router.push('/jobs')
    }
  },
  methods :{
    onSubmit(evt){
      evt.preventDefault()
      console.log(this.job.dxfFile)
      this.view = false
      let self = this
      if(this.job.jobType === 'laser_cutting'){
        if(this.job.scale && this.job.material && this.job.tat && this.job.thick  && this.job.jobPic1 && this.job.jobPic2 && this.job.dxfFile && this.job.description){
        var formData = new FormData();
        formData.append('jobType', this.job.jobType)
        formData.append('scale',this.job.scale)
        formData.append('material',this.job.material)
        formData.append('quantity',this.job.quantity)
        formData.append('tat',this.job.tat)
        formData.append('thick',this.job.thick)
        formData.append('description',this.job.description)
        formData.append("file", this.job.jobPic1);
        formData.append("file1", this.job.jobPic2);
        formData.append("file2", this.job.dxfFile);
      axios.post(this.baseUrl+'job/v1/ops', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
          'authtoken' : this.token
                }
              }).then(function(response){
                self.view = true
                self.toastme("Job Submitted Successfully",'info')
                self.$router.push('/jobs')
          //self.$router.go('/new/job')
        },self)
        .catch(function(e){
          console.log(e.response)
          console.log('FAILURE!!')
          self.view = true
        },self)
        }else{
          this.toastme('Please enter all details','info')
          this.view = true
        }
      }
    },
    toastme(msg,ic){
      this.$toasted.show(msg,{ type : 'info', icon : {
            name : ic
          }}).goAway(8000)
    }
  },
  components:{
    PulseLoader
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.loading {
     position: absolute;
     top: 50%;
     left:50%;
     transform: translate(-50%,-50%);
   }
</style>



