<template>
  <div class="profile">
      <b-container fluid style="margin-top:20px">
            <b-card title="Profile"
                    :sub-title=companyName>
                <p class="card-text text-left">
                    <b>Name</b> : {{name}} <br>
                    <b>Email</b> : {{email}} <br>
                    <b>Mobile</b> : {{mobile}} <br>
                    <b>Address</b> : {{address}} <br>
                    <b>City</b> : {{city}}
                </p>
            </b-card>
      </b-container>
      <b-navbar fixed="bottom">
      <span >Copyrights @ Upyog 2018</span>
    </b-navbar>
  </div>
</template>

<script>
import {store} from '../store/store'
import axios from 'axios'
export default {
  name: 'Profile',
  data () {
    return {
      baseUrl:'',
      msg: 'Welcome to Your Profile Vue.js App',
      token : '',
      name : '',
      email :'',
      mobile: '',
      companyName : '',
      address: '',
      city : ''
    }
  },
  created(){
    this.baseUrl = this.$baseIp()
    this.token = store.getters.getToken
  },
  mounted(){
    let my = this
    axios({
      method: 'get',
      url: this.baseUrl+'user/v1/ops',
      headers:{
        'Content-Type':'application/json',
        'authtoken':this.token
      }
    }).then(function(response){
      my.name = response.data.data.name
      my.email = response.data.data.email
      my.mobile = response.data.data.mobile
      my.companyName = response.data.data.companyName
      my.address = response.data.data.address
      my.city = response.data.data.city
    },my).catch(function(error){
      console.log(error.response.status)
      if(error.response.status === 401){
        my.$router.push('/logout')
      }else{
        my.toastme('Please try again later','info')
      }
    },my)
  },
  methods:{
    toastme(msg,ic){
      this.$toasted.show(msg,{ type : 'info', icon : {
            name : ic
          }}).goAway(8000)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

</style>



