<template>
  <div class="BidDetail">
      <b-container style="margin-top:20px">
        <b-row>
          <b-col>
              <b-card title='QUOTE'
                    img-alt="Image"
                    img-top
                    tag="article"
                    style="max-width: 30rem;"
                    class="mb-2">
                    <hr>
                <p class="card-text text-left">
                  <!--<strong>ID</strong> : {{bid}} <br> -->
                  <strong>UNIT PRICE</strong> : {{unitPrice}}  Rs<br>
                  <strong>TOTAL PRICE</strong> : {{totalPrice}} Rs<br>
                  <strong>TIME</strong> : {{tat}} Days <br>
                  <strong>DESCRIPTION</strong> : {{description}}
                </p>
                <b-button v-if="selected === false && cat === 'seeker'" @click="acceptJob" variant="outline-success" :block=true class="left">ACCEPT</b-button>
                <br>
                <b-button v-b-modal.modal1 v-if="selected === true" href="#" variant="outline-info" :block=true class="right">FEEDBACK</b-button>
                <b-button v-b-modal.modalQuote v-if="cat === 'provider' && jobStatus === true" variant="outline-info">EDIT</b-button>
              </b-card>
          </b-col>
          <b-col>
            <div class="tww">
            <div v-for="item in discussion" class="text-left">
              <b>{{ item.name }}</b> says: {{item.msg }}
            </div>
            </div>
            <!-- <b-table hover responsive small :items="discussion" :fields="fields" class="tw"></b-table> -->
            <b-form @submit="disc">
              <b-form-group>
                <b-form-textarea id="textarea1"
                     v-model="msg"
                     placeholder="Enter Message"
                     :rows="3"
                     :max-rows="6">
              </b-form-textarea>
              </b-form-group>
              <b-form-group>
                <b-btn type="submit" lg :block=true variant="success">SUBMIT</b-btn>
              </b-form-group>
            </b-form>
          </b-col>
        </b-row>
      </b-container>
      <b-modal id="modal1" title="FeedBack" hide-footer ref="myModalRef" class="text-center">
          <b-form @submit="fbSubmit">
              <b-form-group>
                <span class="text-left">Company Proficiency</span>
                <star-rating @rating-selected="setRating" :increment="0.5"></star-rating>
              </b-form-group>
              <b-form-group>
                Job Precision
                <star-rating @rating-selected="setRating" :increment="0.5"></star-rating>
              </b-form-group>
              <b-form-group>
                Job Satisfaction
                <star-rating @rating-selected="setRating" :increment="0.5"></star-rating>
              </b-form-group>
              <b-form-group>
                <b-form-textarea type="text"
                            v-model="feedback"
                            required
                            :rows="3"
                            :max-rows="6"
                            placeholder="FeedBack/Ask">
                </b-form-textarea>
            </b-form-group>
            <b-button type="submit" variant="primary" @click="hideModal" >Submit</b-button>
          </b-form>
      </b-modal>
      <b-modal id="modalQuote" title="Quote" hide-footer ref="myModalRef">
        <b-form @submit="quoteSubmit">
      <b-form-group id="unitPrice"
                      label="Unit Price">
        <b-form-input type="number"
                      v-model="form.unitPrice"
                      required
                      placeholder="Enter Unit Price">
        </b-form-input>
      </b-form-group>
      <b-form-group
                  label="Time in Days">
        <b-form-input type="number"
                      v-model="form.tat"
                      required
                      placeholder="Please mention Turn Around Time in Days">
        </b-form-input>
      </b-form-group>
      <b-button type="submit" variant="primary"  >Submit</b-button>
    </b-form>
    </b-modal>
    <loading
     :show="lshow"
     :label="label">
    </loading>
    <footer></footer>
  </div>
</template>

<script>
import {store} from '../store/store'
import axios from 'axios'
import swal from 'sweetalert2'
import Loading from 'vue-full-loading'
import Footer from './Footer'
export default {
  name: 'BidDetail',
  data () {
    return {
      lshow : false,
      label : 'Loading',
      baseUrl : '',
      selected : false,
      providerId : '',
      feedback: '',
      jp : 5,
      cp : 5,
      js : 5,
      msg: 'Welcome to Your BidDetail Vue.js App',
      perpage: 4,
      bid : '',
      jid : '',
      cat : '',
      token : '',
      unitPrice : '',
      totalPrice : '',
      quantity : '',
      description : '',
      tat: '',
      jobStatus : false,
      text : '',
      discussion : [],
      form :{
        unitPrice:'',
        description:'',
        tat : ''
      },
      msg : '',
      fields : [{key:'name',label:''},{key:'msg',label:''}]
    }
  },
  created(){
    this.baseUrl = this.$baseIp()
    this.bid = this.$route.params.bidId
    this.cat = store.getters.getCat
    this.token = store.getters.getToken
  },
  mounted(){
    let my = this
    axios.get(this.baseUrl+`bid/v1/ops/${this.bid}`,{
      headers: {
        'Content-Type': 'application/json',
        'authtoken' : this.token
      }
      })
      .then(function (response) {
        //console.log(response);
        console.log(response.data)
        my.bid = response.data.data.bidId
        my.unitPrice = response.data.data.unitPrice
        my.totalPrice = response.data.data.totalPrice
        my.description = response.data.data.description
        my.discussion = response.data.data.discussion
        my.selected = response.data.data.selected
        my.jid = response.data.data.jobId
        my.providerId = response.data.data.providerId
        my.jobStatus = response.data.data.jobStatus,
        my.quantity = response.data.data.quantity
        my.form.unitPrice = response.data.data.unitPrice
        my.form.tat = response.data.data.tat
        my.tat = response.data.data.tat
      },my)
      .catch(function (error) {
        my.toastme("please try again later"+error,'info')
      },my);
      /*this.$router.get(`139.59.16.210:3232/bid/v1/ops/${this.bid}`,{headers:{"Content-Type":"application/json","authtoken":this.token}}).then(
        response => {

        },
        response=>{

        }) */
  },
  methods:{
    fbSubmit(evt){
      evt.preventDefault()
      console.log(`${cp} ${jp} ${js}`)
    },
    setRating(evt,rating){
      console.log(rating)
    },
    disc(evt){
      evt.preventDefault()
      let my = this
      if(my.msg){
        axios({
          method: 'post',
          url: this.baseUrl+'bid/v1/ops/message',
          data: {
            bidId : this.bid,
            msg : this.msg
          },
          headers: {
            'Content-Type': 'application/json',
            'authtoken' : this.token
          }
        }).then(function (response) {
          console.log(response);
          my.$router.go(`/bid/${my.bid}`)
        },my)
        .catch(function (error) {
          console.log("a");
          console.log(error.response);
          my.toastme('Please try again later','info')
        },my);
      }else{
        my.toastme('Please enter message','info')
      }
      /*axios.post(`http://139.59.16.210:3232/bid/v1/ops/message`,{
          bidId : this.bid,
          msg : this.msg,
        headers: {
          'Content-Type': 'application/json',
          'authtoken' : this.token
        }
      })
      .then(function (response) {
        console.log(response);
      })
      .catch(function (error) {
        console.log("a");
        console.log(error.response);
      });*/
    },
    acceptJob(){
      let my = this
      swal({
        title: 'Are you sure you want to accept this quote?',
        text: "",
        type: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes, accept it!'
      }).then((result) => {
        console.log(`${this.bid} ${this.jid} ${this.providerId}`)
        if (result.value && this.bid && this.jid) {
          axios({
            method: 'post',
            url: this.baseUrl+'bid/v1/ops/accept',
            data: {
              bidId : this.bid,
              jobId : this.jid,
              providerId : this.providerId,
            },
            headers: {
              'Content-Type': 'application/json',
              'authtoken' : this.token
            }
          }).then(function (response) {
            console.log(response);
            my.$router.go(`/bid/${my.bid}`)
          },my)
          .catch(function (error) {
            console.log("a");
            console.log(error.response);
            my.toastme('Please try again later','info')
          },my);
        }else{
          console.log("cancelled")
        }
      })
    },
    toastme(msg,ic){
      this.$toasted.show(msg,{ type : 'info', icon : {
            name : ic
          }}).goAway(5000)
    },
    hideModal () {
      this.$refs.myModalRef.hide()
    },
    quoteSubmit(evt){
      evt.preventDefault()
      this.$refs.myModalRef.hide()
      this.lshow = true
      let self  = this
      let a = new FormData()
      a.set('unitPrice', this.form.unitPrice)
      a.set('tat',this.form.tat)
      axios({
        method : 'post',
        data : {
          bidId : this.bid,
          unitPrice: this.form.unitPrice,
          quantity: this.quantity,
          tat: this.form.tat
        },
        url : this.baseUrl+'bid/v1/ops/put',
        headers: {
          'Content-Type': 'application/json',
          'authtoken' : this.$ls.get('token')
            }
      }).then(function(result){console.log(result);self.lshow=false;self.$router.go(`/bid/${self.bid}`)},self)
      .catch(function(err){console.log(err);self.lshow=false;self.toastme("Please try again later",'info')},self)
    }
  },
  components:{
    Loading,
    Footer
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.tw {
   width:100%;
   height:calc(100% - 400px);
   overflow-y: scroll;
}
.tww {
  height: 400px;
  overflow-y: scroll;
}
</style>



