import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Login from '@/components/Login'
import Test from '@/components/Test'
import Job from '@/components/Job'
import JobDetail from '@/components/JobDetail'
import NewJob from '@/components/NewJob'
import BidDetail from '@/components/BidDetail'
import Profile from '@/components/Profile'
import {store} from '@/store/store'
import pouchdb from 'pouchdb'
import VueResource from 'vue-resource'
import History from '@/components/History'
import SecureLs from 'secure-ls'
Vue.use(Router)
Vue.use(VueResource)

const R = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      beforeEnter: (to, from, next) => {
        if (store.getters.getAuth) {
          next()
        } else {
          next('/login')
        }
      }
    },
    {
      path: '/jobs',
      name: 'Job',
      component: Job,
      beforeEnter: (to, from, next) => {
        if (store.getters.getAuth) {
          next()
        } else {
          next('/login')
        }
      }
    },
    {
      path: '/job/:jobId',
      name: 'JobDetail',
      component: JobDetail,
      beforeEnter: (to, from, next) => {
        if (store.getters.getAuth) {
          next()
        } else {
          next('/login')
        }
      }
    },
    ,
    {
      path: '/new/job',
      name: 'NewJob',
      component: NewJob,
      beforeEnter: (to, from, next) => {
        if (store.getters.getAuth) {
          next()
        } else {
          next('/login')
        }
      }
    },
    {
      path: '/bid/:bidId',
      name: 'BidDetail',
      component: BidDetail,
      beforeEnter: (to, from, next) => {
        if (store.getters.getAuth) {
          next()
        } else {
          next('/login')
        }
      }
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/profile',
      name: 'Profile',
      component: Profile,
      beforeEnter: (to, from, next) => {
        if (store.getters.getAuth) {
          next()
        } else {
          next('/login')
        }
      }
    },
    {
      path: '/history',
      name: 'History',
      component: History,
      beforeEnter: (to, from, next) => {
        if (store.getters.getAuth) {
          next()
        } else {
          next('/login')
        }
      }
    },
    {
      path: '/logout',
      name: 'Logout',
      beforeEnter: (to, from, next) => {
        store.commit('setAuth', false)
        store.commit('setToken', '12345')
        store.commit('setJobSync', '')
        store.commit('setCat', '')
        store.commit('setJobSyncStatus', true)
        var ls = new SecureLs({encodingType: 'aes'});
        ls.removeAll()
        new pouchdb('db').erase().then(function () {
          next('/login')
        }).catch(function (err) {
          next('/login')
        })
      }
    },
    {
      path: '/test',
      name: 'Test',
      component: Test
    }
  ]
})

Vue.http.interceptors.push(function(request, next) {
  // continue to next interceptor
  next(function(response) {
    /*console.log("-----Interceptor----")
    console.log(response)
    console.log("-----Interceptor----") --> */
      if(response.status == 401 || response.status == 406){
        R.push('/logout')
      }
  });
})

export default R



