            text-variant="info"
            class="">
            <span class="cardstyle">
              {{lj}}
            </span><br>
            <small>*Published/Available</small><br>
            <b-button href="/jobs" variant="outline-info">JOB LIST</b-button>
          </b-card>
          <b-card v-if="cat === 'provider'"
