            text-variant="info"
            class="">
            <span class="cardstyle">
              {{plan}}
            </span> <br>
            <small>*You get 10 Job Posts/Quotes</small> <br>
            <b-button variant="outline-info">FAQ</b-button>
          </b-card>
          <b-card v-if="cat === 'seeker'"
