            <b-button variant="outline-info">BREAK UP</b-button>
          </b-card>
      </b-card-group>
     <!-- <b-row>
      <b-col>
          <b-card
            header="PLAN"
            text-variant="info"
            class="mb-1">
            <p class="card-text cardstyle">
              {{plan}}
            </p>
            <p>*You get 10 Job Posts/Quotes</p>
            <b-button variant="outline-info">FAQ</b-button>
          </b-card>
      </b-col>
      <b-col>
          <b-card
            header="PLAN"
            text-variant="primary"
            class="mb-1">
            <p class="card-text cardstyle">
              {{plan}}
            </p>
            <p>*You get 10 Job Posts/Quotes</p>
            <b-button variant="outline-primary">FAQ</b-button>
          </b-card>
      </b-col>
      <b-col>
          <b-card
            header="PLAN"
            text-variant="primary"
            class="mb-1">
            <p class="card-text cardstyle">
              {{plan}}
            </p>
            <p>*You get 10 Job Posts/Quotes</p>
            <b-button variant="outline-primary">FAQ</b-button>
          </b-card>
      </b-col>
      </b-row> -->
    </b-container>
    <b-container class="conatiner">
      <b-row align-h="start">
        <b-col>
          <highcharts :options="options" ref="highcharts"></highcharts>
        </b-col>
        <b-col>
          <!-- <highcharts :options="sideOptions" ref="highcharts"></highcharts> -->
        </b-col>
      </b-row>
      <!--<b-row>
        <b-col>
          <highcharts :options="pieOptions" ref="highcharts"></highcharts>
        </b-col>
      </b-row> -->
    </b-container>
    <div>
    <b-navbar type="light"  fixed="bottom" align-h="center">
      <span class="text-center">Copyrights @ Upyog 2018</span>
    </b-navbar>
</div>
