<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      circle: false,
      msg: 'Welcome to Your Vue.js App',
      plan : '999',
      lj : 10,
      quotes : 1000,
      cat : '',
      db : '',
      options: {
        colors: ['blue','orange','green','red'],
        plotOptions: {
          column: {
            colorByPoint: true
          }
        },
        chart: {
            type: 'column'
        },
        title: {
            text: 'Jobs'
        },
        xAxis: {
            type: 'category',
            labels: {
                rotation: -45,
                style: {
                    fontSize: '13px',
                    fontFamily: 'Verdana, sans-serif'
                }
            }
        },
        yAxis: {
            min: 0,
            title: {
                text: 'Units'
            }
        },
        legend: {
            enabled: false
        },
        tooltip: {
            pointFormat: '<b>{point.y:.1f}</b>'
        },
        series: [{
            name: 'Jobs',
            data: [
                ['Total Jobs', 24],
                ['Open Jobs', 10],
                ['Closed Jobs', 5],
                ['Cancelled Jobs', 5, {color :'red'}],
            ],
            dataLabels: {
                enabled: true,
                rotation: -90,
                color: '#FFFFFF',
                align: 'right',
                format: '{point.y:.1f}', // one decimal
                y: 10, // 10 pixels down from the top
                style: {
                    fontSize: '13px',
                    fontFamily: 'Verdana, sans-serif'
                }
            }
        }]
      },
      pieOptions: {
        chart: {
            plotBackgroundColor: null,
            plotBorderWidth: 0,
            plotShadow: false
        },
        title: {
            text: 'Job<br>Price<br>Breakdown',
            align: 'center',
            verticalAlign: 'middle',
            y: 40
        },
        tooltip: {
            pointFormat: '{series.name}: <b>{point.percentage:.1f}%</b>'
        },
        plotOptions: {
            pie: {
                dataLabels: {
                    enabled: true,
                    distance: -50,
                    style: {
                        fontWeight: 'bold',
                        color: 'white'
                    }
                },
                startAngle: -90,
                endAngle: 90,
                center: ['50%', '75%']
            }
        },
        series: [{
            type: 'pie',
            name: 'Job Price Breakdown',
            innerSize: '50%',
            data: [
                ['<200 to >500', 50],
                ['<500 to >1000', 40],
                ['<1000',10 ],
            ]
        }]
      },
      sideOptions: {
        colors: ['blue','orange','green'],
        chart: {
            type: 'bar'
        },
        title: {
            text: 'Insights'
        },
        xAxis: {
            categories: ['Profile Views', 'Bids', 'Interactions'],
            title: {
                text: null
            }
        },
        yAxis: {
            min: 0,
            title: {
                text: 'units',
                align: 'high'
            },
            labels: {
                overflow: 'justify'
            }
        },
        tooltip: {
            valueSuffix: ' millions'
        },
        plotOptions: {
            bar: {
                dataLabels: {
                    enabled: true
                }
            },
            column: {
                colorByPoint: true
              }
        },
        legend: {
            layout: 'vertical',
            align: 'right',
            verticalAlign: 'top',
            x: -40,
            y: 80,
            floating: true,
            borderWidth: 1,
            shadow: true
        },
        credits: {
            enabled: false
        },
        series: [{
            name: 'Live Data',
            data: [107, 31, 635]
        }]
    }
    }
  },
  created(){
    this.cat = this.$ls.get('cat')
  },
  mounted(){
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.container {
  margin-top: 2%
}
.cardstyle {
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-size: 1cm
}
.mydeck {
  height: 13%;
}
</style>



