<template>
  <div class="hello">
      <b-container>
        <b-table bordered responsive hover :items="items" :fields="fields" :current-page="currentPage" :per-page="perPage" >
          <template slot="index" slot-scope="data">
            {{data.index + 1}}
          </template>
          <template slot="quotes" slot-scope="cell">
            <!-- We use click.stop here to prevent a 'row-clicked' event from also happening -->
            <b-button size="sm" variant="info" @click.stop="details(cell.item,cell.index,$event.target)">Quotes</b-button>
          </template>
        </b-table>
        <b-pagination fixed="bottom" align="center" v-if="items && items.length > 0" :total-rows="items.length" :per-page="perPage" v-model="currentPage" />
      </b-container>
      <loading
        :show="lshow"
        :label="label">
      </loading>
      <Footer></Footer>
  </div>
</template>

<script>
import Footer from './Footer'
import {store} from '../store/store'
import moment from 'moment'
import Loading from 'vue-full-loading'
export default {
  name: 'History',
  data () {
    return {
      msg: 'Welcome to Your history Vue.js App',
      items: [],
      fields:['index',"jobType","material",'published','status','quotes',{key:'createdAt',sortable:true, formatter: "showTime"}],
      currentPage : 1,
      perPage : 10,
      baseUrl:'',
      lshow : false,
      label : "Loading"
    }
  },
  created(){
    this.baseUrl = this.$baseIp()
    console.log(this.baseUrl)
    this.cat = store.getters.getCat
    let _then = this.$moment(store.getters.getHJobSync)
    let self = this
    let _now = this.$moment(this.$moment.now())
    console.log(_now.diff(_then))
    let _diff = _now.diff(_then)
    if( _diff > 3000){
      this.lshow = true
      let now = this.$moment.now()
      store.commit('setHJobSync',now)
      this.$hdb.erase().then(function(result){self.jobSyncCall()},self).catch(function(err){self.$router.push('/login')},self)
    }else{
      this.lshow = true
      console.log('local fetch')
      this.$hdb.allDocs({include_docs: true})
      .then(function(result){
        result.rows.forEach(function(doc){
          self.items.push(doc.doc)
        })
        self.lshow = false
      },self)
      .catch(function(err){console.log(err);self.toastme("Please try again later",'info');self.lshow = false},self)
    }

  },
  computed:{

  },
  methods:{
    toastme(msg,ic){
      this.$toasted.show(msg,{ type : 'info', icon : {
            name : ic
          }}).goAway(5000)
    },
    jobSyncCall(){
      let self = this
      let token = store.getters.getToken
      this.$http.get(this.baseUrl+'job/v1/ops/history',{headers:{'Content-Type':"application/json",authtoken:token}}).then(response => {
      this.items = response.body.data
      console.log(response)
      if(!this.items.length > 0){
        this.toastme('No Jobs available','info')
        this.lshow = false
      }
      console.log(response.status)
      this.$hdb.bulkDocs(response.body.data).then(function (result) {console.log(result);self.lshow = false},self)
        .catch(function (err) {
          this.$toasted.show('Please Try Again Later',{ type : 'warn', icon : {
            name : 'cancel',
            after : true // this will append the icon to the end of content
          }}).goAway(5000)
          self.lshow = false
        },self);
      }, response => {
        // error callback
        console.log(response.status)
        if(response.status === 401 || response.status === 500 || response.status === 500){
          this.$router.push('/logout')
        }else{
          this.$toasted.show('Please Try Again Later',{ type : 'warn', icon : {
            name : 'cancel',
            after : true // this will append the icon to the end of content
          }}).goAway(5000)
          this.lshow = false
        }
      });
    },
    checkEdited(item){
      let a = this.$moment(item.createdAt)
      let b = this.$moment(item.updatedAt)
      if(a.diff(b) > 0 ){
        item._rowVariant = 'warning'
        return 'Edited'
      }else{
        return 'Original'
        this.showEdited = false
      }
    },
    selected(record,index){
      this.$toasted.show("lol").goAway(1000)
    },
    mt(item){
      return moment(item).format('MMMM Do YYYY, h:mm:ss a')
    },
    details(a,c,b){
      console.log(a._id)
      this.$router.push({name:'JobDetail',params:{jobId:a._id}})
    },
    showTime(value){
      return moment(value).format('MMMM Do YYYY, h:mm:ss a')
    }
  },
  components:{
    Footer,
    Loading
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



