<template>
  <div class="login">
    <b-container class="example" fluid>
      <b-row align-h="center">
        <b-col cols="4">
          <h1>{{ msg }}</h1>
          <b-form @submit="onSubmit"  v-if="show">
            <b-form-group id="email"
                    label="Email:"
                    label-for="email">
                      <b-form-input id="email"
                        type="email"
                        v-model="form.email"
                        required
                        placeholder="Enter email">
                      </b-form-input>
            </b-form-group>
            <b-form-group id="password"
                          label="Password"
                          label-for="password">
                            <b-form-input id="password"
                              type="password"
                              v-model="form.password"
                              required
                              placeholder="Enter Password">
                            </b-form-input>
            </b-form-group>
            <br>
      <b-button type="submit" :block=true variant="primary">Submit</b-button>
    </b-form>
        </b-col>
      </b-row>
    </b-container>
     <loading
     :show="lshow"
     :label="label">
    </loading>
    <!--<FadeLoader  class="perfect" :height="height" :width="width"></FadeLoader> -->
    <!--<Origami class="mx-auto position-relative" :size='size'></Origami> -->
    <!--<b-table striped hover  @row-clicked="selected" :items="items" :fields="fields" :current-page="currentPage" :per-page="perPage">
    </b-table>
    <b-pagination align="center" :total-rows="items.length" :per-page="perPage" v-model="currentPage" /> -->
        <!--<b-form @submit="onFile" enctype="multipart/form-data">
    <b-form-file v-model="file" :state="Boolean(file)" placeholder="Choose a file..."></b-form-file>
      <div class="mt-3">Selected file: {{file && file.name}}</div>
      <b-form-file v-model="file1" :state="Boolean(file1)" placeholder="Choose a file..."></b-form-file>
      <div class="mt-3">Selected file: {{file1 && file1.name}}</div>
            <b-button type="submit" variant="primary" required>Submit</b-button>
        </b-form> -->
    <b-navbar  type="light"  fixed="bottom" >
      <span >Copyrights @ Upyog 2018</span>
    </b-navbar>
  </div>
</template>

<script>
import {store} from '../store/store'
import {RotateSquare2} from 'vue-loading-spinner'
import {Socket} from 'vue-loading-spinner'
import {Origami} from 'vue-loading-spinner'
import PulseLoader from 'vue-spinner/src/PulseLoader.vue'
import FadeLoader from 'vue-spinner/src/FadeLoader.vue'
import myForm from 'form-data'
import axios from 'axios'
import Loading from 'vue-full-loading'

export default {
  name: 'Login',
  data () {
    return {
      lshow : false,
      label : 'Loading....',
      overlay : true,
      baseUrl : '',
      file : '',
      file1: '',
      msg: 'LOGIN',
      size:'40px',
      form: {
        email: '',
        password: ''
      },
      items: [
          { isActive: true, age: 40, first_name: 'Dickerson', last_name: 'Macdonald' },
          { isActive: false, age: 21, first_name: 'Larsen', last_name: 'Shaw' },
          { isActive: false, age: 89, first_name: 'Geneva', last_name: 'Wilson' },
          { isActive: true, age: 38, first_name: 'Jami', last_name: 'Carney' }
      ],
    fields:["first_name","last_name",{key:'age',label:"How old are u?"}],
    currentPage: 1,
    perPage: 2,
    show: true,
    height: "40px",
    width:"40px"
    }
  },
  created: function(){
    let my = this
    this.baseUrl = this.$baseIp()
    console.log(this.baseUrl)
    this.$toasted.show('Welcome User',{ type : 'info', icon : {
        name : 'check',
        after : true // this will append the icon to the end of content
    }}).goAway(5000)

    //setTimeout( ()=> {this.lshow = true},100)

    /*this.$worker.run(() => 'this.$worker run 1: Function in other thread')
    .then(console.log) // logs 'this.$worker run 1: Function in other thread'
    .catch(console.error) */
    /*setTimeout(function(){
      my.lshow = false
    },4000).bind(my) */
  },
  methods: {
    onSubmit (evt) {
      evt.preventDefault()
      /*store.commit('setAuth', true)
      this.$router.push('/')*/
      this.lshow = true
      let user = {
        email : this.form.email.trim(),
        password : this.form.password.trim()
      }
      this.$http.post(this.baseUrl+'login',{email:user.email,password:user.password,headers:{'Content-Type':'application/json'}}).then(response => {
        store.commit('setAuth', true)
        store.commit('setToken', response.body.token)
        store.commit('setCat', response.body.cat)
        let now = this.$moment.now()
        store.commit('setJobSync',now)
        store.commit('setHJobSync',now)
        console.log(store.getters.getJobSync)
        this.$ls.set('token',response.body.token)
        this.$ls.set('cat',response.body.cat)
        console.log(this.$ls.get('token'))
        //store.commit('setJobSync',this.$moment.now())
        //store.commit('setJobSyncStatus', true)
        this.lshow = true
        this.dbClear()
        }, response => {
          this.$toasted.show('Please Enter Correct Email/Password',{ type : 'error', icon : {
            name : 'ban',
            after : true // this will append the icon to the end of content
          }}).goAway(10000)
        });
    },
    jobSyncCall(self){
      let token = self.$ls.get('token')
      self.$http.get(self.baseUrl+'job/v1/ops/jobs',{headers:{'Content-Type':"application/json",authtoken:token}}).then(response => {
        console.log(response.body.data)
      if(!response.body.data.length > 0){
        self.toastme('No Jobs available','info')
      }else{
        console.log(response.body.data)
        self.$db.bulkDocs(response.body.data).then(function (result) {console.log(result)})
        .catch(function (err) {
          self.toastme('Please try again Later','cancel')
        });
      }
      self.lshow = false
      self.$router.push({path:'/'})
      }, response => {
        // error callback
        console.log(response.status)
        self.lshow = false
        if(response.status === 401 || response.status === 500 || response.status === 500){
          self.$router.push('/logout')
        }else{
          self.toastme('Please try again later','cancel')
          self.$router.push({path:'/login'})
        }
      });
    },
    toastme(msg,ic){
      this.$toasted.show(msg,{ type : 'info', icon : {
            name : ic
          }}).goAway(8000)
    },
    selected(record,index){
      this.$toasted.show("lol")
    },
    onFile(evt){
      evt.preventDefault()
      var formData = new FormData();

      formData.append("file", this.file);
      formData.append("file1", this.file1);
              axios.post('http://139.59.16.210:3232/img', formData, {
                headers: {
                  'Content-Type': 'multipart/form-data'
                }
              }).then(function(){
          console.log('SUCCESS!!');
        })
        .catch(function(){
          console.log('FAILURE!!');
        });
    },
    dbClear(){
      let self = this
      this.$db.erase().then(function (resp) {
          console.log("called"+resp)
          self.jobSyncCall(self)
        },self).catch(function (err) {
          console.log(err)
          self.$toasted.show('Please Try Again Later',{ type : 'warn',
            icon : {
              name : 'cancel',
              after : true // this will append the icon to the end of content
            }
          }).goAway(5000)
          self.lshow = false
        },self)
    }
  },
  destroyed: ()=>{
    console.log("done")
  },
  components: {
      PulseLoader,
      FadeLoader,
      Loading
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.example {
  margin-top: 4%
}
.perfect {
     position: absolute;
     top: 50%;
     left:50%;
     transform: translate(-50%,-50%);
   }
</style>



