import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'
import _ from 'lodash'

Vue.use(Vuex)

export const store = new Vuex.Store({
  plugins: [createPersistedState()],
  state: {
    isAuth: false,
    token: '1234',
    cat: '',
    jobs:[],
    jobSync:'',
    hJobSync:'',
    jobSyncStatus: true
  },
  getters: {
    getAuth (state) {
      return state.isAuth
    },
    getToken (state) {
      return state.token
    },
    getCat (state) {
      return state.cat
    },
    getJobSync (state) {
      return state.jobSync
    },
    getHJobSync (state) {
      return state.hJobSync
    },
    getJobSyncStatus (state) {
      return state.jobSyncStatus
    }
  },
  mutations: {
    setAuth (state, payload) {
      state.isAuth = payload
    },
    setToken (state, payload) {
      state.token = payload
    },
    setCat (state, payload) {
      state.cat = payload
    },
    setJobs(state, payload){
      _.forEach(payload,function(value){
        state.jobs.push(value)
      })
    },
    setJobSync (state,payload) {
      state.jobSync = payload
    },
    setHJobSync (state,payload) {
      state.hJobSync = payload
    },
    setJobSyncStatus (state,payload) {
      state.jobSyncStatus = payload
    }
  },
  actions: {
  }
})



